@import '../../styles/colors.module.scss';
@import '../../styles/mixins.module.scss';

$cardFullHeight: 320px;
$pictureFullHeight: 220px;
$toCartBtnDimensions: 40px;

.card{
  a{
    position: relative;
    display: block;
    height: 100%;
  }

  .picture{
    width:100%;
    object-fit: fill;
  }

  .productDetails{
    position: relative;
    height: calc(#{$cardFullHeight} - #{$pictureFullHeight});
      
    .title{
      width: 100%;
      margin-bottom: 3px;
    }
  
    .price{
      position: absolute;
      left:1px;
      bottom:10px;
      text-shadow: 0px 0px 1px;
      font-size: 1.5rem;
    }
  }

  .toCartBtn{
    position: absolute;
    top: calc(#{$pictureFullHeight} - #{$toCartBtnDimensions + 10px});
    right:10px;
    width: $toCartBtnDimensions;
    height:  $toCartBtnDimensions;
    border-radius: 50%;
    background-color: black;
    opacity: 0.5;
  
    svg{
      @include centerVH;
    }
  }
}

.cardFull{
  position: relative;
  height: $cardFullHeight;
  width:160px;
  background-color: $white-smoke;
  box-shadow: 0px 0px 5px 0px $phantom-gray;
  font-size: 1.35rem;

  .picture{
    height:$pictureFullHeight;
  }
}

.cardCover{
  position: relative;
  flex-shrink: 0;
  height: 90%;
  box-shadow: 0 0 5px 2px $phantom-gray;

  .picture{
    height:100%;
  }

  .productDetails{
    position: absolute;
    height: 100%;
    width: 100%;
    top:0px;
    left:0px;
    opacity: 0;
    transition: opacity 0.5s;
    background-color: $white-smoke;
    font-size: 1.3rem;

    .title{
      @include ellipsis(3);
    }
  
  }

  .productDetails:hover{
    opacity:0.8;
  }

  .toCartBtn{
    display: none;
  } 
}
 
@media  (min-width:950px) {
  .cardCover{
    .productDetails{
      padding: 10% 5px;
      display: flex;
      flex-direction: column;
      align-items: center;

      .title{
        text-align: center;  
      }

      .price{
        bottom: 15%;
        font-size: 2rem;
        @include centerH;
      }
    }
  }
}